import React, { useEffect, useState, useRef } from 'react'
import './VisitorStats.css' // Import your custom CSS for flip animations

const VisitorStats = () => {
  const [visitorCount, setVisitorCount] = useState(0)
  const socketRef = useRef(null)

  useEffect(() => {
    const wsUrl = process.env.REACT_APP_WS_URL
    if (!wsUrl) {
      console.error("Missing REACT_APP_WS_URL in .env file")
      return undefined // ✅ Fixes ESLint's consistent-return error
    }

    const socket = new WebSocket(wsUrl)
    socketRef.current = socket

    socket.onopen = () => {
      console.log('✅ WebSocket connected')
    }

    socket.onmessage = (event) => {
      try {
        const data = JSON.parse(event.data)
        if (data?.visitorCount !== undefined) {
          setVisitorCount(data.visitorCount)
        }
      } catch (error) {
        console.error('❌ Invalid message received:', event.data)
      }
    }

    socket.onclose = () => {
      console.log('🔌 WebSocket disconnected')
    }

    return () => {
      socket.close()
    }
  }, [])

  return (
    <div className="visitor-stats-container">
      <h2>Live Visitors</h2>
      <div className="flip-card">
        <div className="flip-card-inner">
          <div className="flip-card-front">{visitorCount}</div>
          <div className="flip-card-back">{visitorCount}</div>
        </div>
      </div>
    </div>
  )
}

export default VisitorStats
